.drum-pad{
    width: 70px;
    height: 35px;
    background-color: black;
    margin: 3px;
    color: white;
 
    border: none;
    position:relative;
    display: inline-block;
    left: -30px;
    top: -30px;
    
}


.drum-pad:hover{
background-color: yellow;
cursor: pointer;
}
#text{
    position: absolute;
    left: 10px;
}





